.contact-page {
    width: 80vw;
    margin-top: 5em;
    h1,h2 {
        width: 100%;
    }
    .contact-form{
        .field-set {
            width: 100%;
            padding-left: 0px;
            border: none;
            .field-set-lbl{
                display: block;
                padding-bottom: 10px;
            }
            .field-set-input{
                padding: 5px;
                width: 300px;
            }
            .field-set-txt{
                width: 306px;
                height: 5rem;
            }
        }
    }
}